.container {
    padding: 15px;
}

.comment-block {
    max-height: 500px;
    overflow: auto;
    width: fit-content;
    scroll-behavior: smooth;
    transition: scroll-behavior 0.3s;
}

.item-enter {
    opacity: 0;
}
.item-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}
.item-exit {
    opacity: 1;
}
.item-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
}